  content: "";
  position: absolute;
  top: 0.1vw;
  left: -0.3vw;
  width: 0.5vw;
  height: 0.5vw;
  background-color: var(--orange-color);
  border-radius: 100vw;
}
.hero__text-icon::before {
  content: "";
  position: absolute;
  top: -0.1vw;
  left: -0.5vw;
  width: 0.9vw;
  height: 0.9vw;
  background-color: var(--orange-color);
  opacity: 0.5;
  border-radius: 100vw;
}
.hero__text-icon svg {
  width: 1.5vw;
  height: 1.5vw;
}

.benefits {
  display: flex;
  justify-content: center;
  position: relative;
  border-radius: 0 0 2vw 2vw;
  box-shadow: 0px 0px 28px 10px rgba(163, 172, 178, 0.2);
  padding: 0px 2vw 2vw 2vw;
  background-color: var(--light-color);
}
.benefits__items::before {
  content: "";
  position: absolute;
  top: -40px;
  left: 0;
  width: 100%;
  height: 5vw;
  border-radius: 2vw 2vw 0 0;
  background-color: var(--light-color);
  box-shadow: 0px -15px 28px 10px rgba(216, 222, 225, 0.2);
  transform: skewY(5deg);
}
.benefits__item:not(:last-child) {
  position: relative;
  padding-bottom: 2.3vw;
  box-shadow: 0px 13px 18px -22px rgb(159, 162, 164);
  position: relative;
  margin-bottom: 20px;
}
.benefits__item-text {
  text-align: center;
  font-size: 0.8vw;
}
.benefits__item-text span {
  font-weight: 600;
}

@media (max-width: 992px) {
  .hero {
    min-height: 100vh;
    padding: 0px 20px;
    position: relative;
    background: -webkit-linear-gradient(0deg, rgb(255, 255, 255), rgb(86, 181, 184));
    background: -moz-linear-gradient(0deg, rgb(255, 255, 255), rgb(86, 181, 184));
    background: linear-gradient(0deg, rgba(255, 255, 255, 0), #edf2f5), url("../img/bg.jpg");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
  }
  .hero__text {
    width: 50%;
  }
  .hero__inner {
    padding-top: 125px;
    flex-direction: column-reverse;
    align-items: center;
    padding-bottom: 150px;
  }
  .hero__main {
    align-items: center;
  }
  .hero__descr {
    text-align: center;
    width: 100%;
    margin-bottom: 30px;
  }
  .hero__descr .hero__title {
    font-size: 20px;
    margin-bottom: 10px;
  }
  .hero__descr .hero__subtitle {
    margin-bottom: 0px;
    justify-content: center;
  }
  .hero__descr .hero__subtitle h4 {
    font-size: 15px;
    margin-left: 10px;
  }
  .hero__subtitle-figure {
    width: 10px;
    height: 10px;
    border-radius: 100%;
    margin-right: 10px;
  }
  .hero__btns {
    flex-direction: column;
    margin-bottom: 20px;
    order: 2;
  }
  .hero__btn:not(:last-child) {
    margin-right: 1vw;
  }
  .hero__text {
    order: 1;
    max-width: 80%;
    width: 100%;
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
  }
  .hero__text p {
    font-size: 14px;
  }
  .hero__text-icon {
    margin-right: 0px;
    margin-bottom: 20px;
  }
  .hero__text-icon::after {
    top: 3px;
    left: -1px;
    width: 5px;
    height: 5px;
  }
  .hero__text-icon::before {
    top: 0.5px;
    left: -3.3px;
    width: 10px;
    height: 10px;
    border-radius: 100%;
  }
  .hero__text-icon svg {
    width: 20px;
    height: 20px;
  }
  .hero__benefits {
    position: absolute;
    bottom: 0;
    z-index: 3;
    transform: translateY(77%);
  }
  .benefits--show {
    transform: translateY(0);
  }
  .benefits--show .benefits__btn svg {
    transform: rotate(45deg);
    transition: all 0.7s;
  }
  .benefits {
    border-radius: 0 0 2vw 2vw;
    padding: 0px 20px 20px;
    box-shadow: 0px 0px 75px rgba(0, 0, 0, 0.03);
    background-color: var(--light-color);
    margin-right: 0px;
    transition: all 0.7s;
    width: 250px;
  }
  .benefits__items {
    padding-top: 30px;
  }
  .benefits__items::before {
    top: -40px;
    height: 60px;
    border-radius: 20px 20px 0 0;
    transform: skewY(5deg);
  }
  .benefits__item {
    max-width: 180px;
  }
  .benefits__item:not(:last-child) {
    margin-bottom: 15px;
    padding-bottom: 20px;
  }
  .benefits__item-text {
    font-size: 14px;
    text-align: left;
  }
}
@media (max-width: 376px) {
  .hero__descr .hero__title {
    font-size: 21px;
  }
  .hero__descr .hero__subtitle h4 {
    font-size: 15px;
  }
  .hero__descr .hero__subtitle-figure {
    width: 7px;
    height: 7px;
  }
  .hero__text {
    max-width: 100%;
  }
}
@media (max-width: 340px) {
  .hero {
    min-height: 700px;
    padding-bottom: 100px;
  }
  .benefits__item-text {
    font-size: 13px;
  }
}